.toc {
	overflow-y: auto
}

.toc>.toc-list {
	overflow: hidden;
	position: relative
}

.toc>.toc-list li {
	list-style: none
}

.toc-list {
	margin: 0;
	padding-left: 10px
}

a.toc-link {
	color: currentColor;
	height: 100%;
	display: none;
}

a.toc-link.is-active-link {
	display: inline-block;
}

.is-collapsible {
	max-height: 1000px;
	overflow: hidden;
	transition: all 300ms ease-in-out
}

.is-collapsed {
	max-height: 0
}

.is-position-fixed {
	position: fixed !important;
	top: 0
}

.is-active-link {
}

.toc-link::before {
}

.is-active-link::before {
	background-color: #54BC4B
}


