/*

Name:       Base16 Bright Dark
Author:     Chris Kempson (http://chriskempson.com)

Prism template by Bram de Haan (http://atelierbram.github.io/syntax-highlighting/prism/)
Original Base16 color scheme by Chris Kempson (https://github.com/chriskempson/base16)

*/
code[class*="language-"],
pre[class*="language-"] {
	text-shadow: none;
  font-family: var(--font-mono);
  line-height: 1.35;
  direction: ltr;
  text-align: left;
  white-space: pre;
  word-spacing: normal;
  word-break: normal;
  -moz-tab-size: 4;
  -o-tab-size: 4;
  tab-size: 4;
  -webkit-hyphens: none;
  -ms-hyphens: none;
  hyphens: none;
  background: #000000;
  color: #e0e0e0;
	font-weight: 300;
}

pre[class*="language-"]::-moz-selection, pre[class*="language-"] ::-moz-selection,
code[class*="language-"]::-moz-selection, code[class*="language-"] ::-moz-selection {
  text-shadow: none;
	background: var(--color-selection-code);
}

pre[class*="language-"]::selection, pre[class*="language-"] ::selection,
code[class*="language-"]::selection, code[class*="language-"] ::selection {
  text-shadow: none;
	background: var(--color-selection-code);
}

/* Code blocks */
pre[class*="language-"] {
  padding: 1em;
  margin: .5em 0;
  overflow: auto;
}

/* Inline code */
:not(pre) > code[class*="language-"] {
  padding: .1em;
  border-radius: .3em;
}

.token.comment,
.token.prolog,
.token.doctype,
.token.cdata {
  color: #b0b0b0;
}

.token.punctuation {
  color: #e0e0e0;
}

.token.namespace {
  opacity: .7;
}

.token.operator,
.token.boolean,
.token.number {
  color: #fc6d24;
}

.token.property {
  color: #fda331;
}

.token.tag {
  color: #6fb3d2;
}

.token.string {
  color: #76c7b7;
}

.token.selector {
  color: #d381c3;
}

.token.attr-name {
  color: #fc6d24;
}

.token.entity,
.token.url,
.language-css .token.string,
.style .token.string {
  color: #76c7b7;
}

.token.attr-value,
.token.keyword,
.token.control,
.token.directive,
.token.unit {
  color: #a1c659;
}

.token.statement,
.token.regex,
.token.atrule {
  color: #76c7b7;
}

.token.placeholder,
.token.variable {
  color: #6fb3d2;
}

.token.deleted {
  text-decoration: line-through;
}

.token.inserted {
  border-bottom: 1px dotted #ffffff;
  text-decoration: none;
}

.token.italic {
  font-style: italic;
}

.token.important,
.token.bold {
  font-weight: bold;
}

.token.important {
  color: #fb0120;
}

.token.entity {
  cursor: help;
}

pre > code.highlight {
  outline: 0.4em solid #fb0120;
  outline-offset: .4em;
}

.line-numbers .line-numbers-rows {
  border-right-color: #303030 !important;
}

.line-numbers-rows > span:before {
  color: #505050 !important;
}

.line-highlight {
  background: rgba(255, 255, 255, 0.2) !important;
  background: -webkit-linear-gradient(left, rgba(255, 255, 255, 0.2) 70%, rgba(255, 255, 255, 0)) !important;
  background: linear-gradient(to right, rgba(255, 255, 255, 0.2) 70%, rgba(255, 255, 255, 0)) !important;
}
